import React from 'react';
import { Graph } from '../components/Graph';

class ApiHome extends React.Component {
    render() {
        return (
            <div>
                <div class="container">
                <h1>API HOME</h1>
                <h2>Uitleg</h2>
                <br /><br />

                <p>Het aanvragen van een grafiek gaat via de URL.</p>
                <p>[BASEURL]/dashboard#/api/[GRAPHTYPE]/[DATASET]</p>
                <br />

                <p>[GRAPHTYPE] lijst</p>
                <ul>
                    <li>barchart</li>
                    <li>doughnut</li>
                    <li>graph</li>
                    <li>polarpie</li>
                    <li>radar</li>
                    <li>scatter</li>
                </ul>

                <p>De dataset is [d] met een nummer die de desbetreffende dataset aangeeft. [d1] is de eerste set.</p>
                <br/>

                <p><strong>Voorbeeld om een graph met dataset 1 aan te vragen</strong></p>
                <p>localhost:3000/dashboard#/api/graph/d1</p>
                </div>
            </div>
        );
    }
}
export default ApiHome;



