import React from 'react';
import { Radar } from 'react-chartjs-2';

const d1 = {
  labels: ['Eten', 'Drinken', 'Slapen', 'Designen', 'Coden', 'Fietsen', 'Sporten'],
  datasets: [
    {
      label: 'Test 1',
      backgroundColor: 'rgba(179,181,198,0.2)',
      borderColor: 'rgba(179,181,198,1)',
      pointBackgroundColor: 'rgba(179,181,198,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(179,181,198,1)',
      data: [12, 90, 45, 81, 5, 87, 55]
    },
    {
      label: 'Test 2',
      backgroundColor: 'rgba(255,99,132,0.2)',
      borderColor: 'rgba(255,99,132,1)',
      pointBackgroundColor: 'rgba(255,99,132,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(255,99,132,1)',
      data: [70, 65, 90, 23, 96, 54, 34]
    }
  ]
};

// Genereert de image
function done() {
    var url=this.chart.toBase64Image();
    document.getElementById("graph").src=url;
}
// Roept methode done aan zodra de animatie compleet is. Options is ook om grafiekeigenschappen aan te passen voor de api.
var apiOptions = {
    chartArea: {
			backgroundColor: 'rgba(255, 255, 255, 1)'
				},
    animation: {
        onComplete: done
    }
}

// Roept de methode done niet aan omdat het dashboard het niet nodig heeft. Options is ook om grafiekeigenschappen aan te passen voor het dashboard.
var dashboardOptions = {
}

// dataSet en options worden in de willMount geselecteerd afhankelijk van de props.
let dataSet;
let options;

export class Radars extends React.Component {
    // Voor het renderen word de dataset geselecteerd. Dataset is de parameter in de url.
    componentWillMount() {
        // apiOptions genereert een afbeelding, dashboardOptions doet dat niet.
        switch(this.props.goal) {
            case "dashboard":
                options = dashboardOptions;
                break;
            case "api":
                options = apiOptions;
                break;
            default:
                options = dashboardOptions;
        }
        // 'id' geeft de dataset aan. indien niet aangegeven of foutief word het d1.
        switch(this.props.id) {
            case "d1":
                dataSet = d1;
                break;
            case "d2":
                dataSet = ["TestSet"];
                break;
            default:
                dataSet = d1;
            }
        };
    render() {
        return (
            <Radar ref={ref => this.chart = ref} data={dataSet} options={options} />
        );
    }
};



