import React from 'react';
import { Bar } from 'react-chartjs-2';

const d1 = {
  labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
  datasets: [
    {
      label: 'Aantal kwH per maand',
      backgroundColor: 'rgba(255,99,132,0.2)',
      borderColor: 'rgba(255,99,132,1)',
      borderWidth: 1,
      hoverBackgroundColor: 'rgba(255,99,132,0.4)',
      hoverBorderColor: 'rgba(255,99,132,1)',
      data: [123, 55, 80, 81, 105, 34, 95]
    }
  ]
};

// Genereert de image
function done() {
    var url=this.chart.toBase64Image();
    document.getElementById("graph").src=url;
}
// Roept methode done aan zodra de animatie compleet is. Options is ook om grafiekeigenschappen aan te passen voor de api.
var apiOptions = {
    bezierCurve : false,
    width:100,
    height:200,
    maintainAspectRatio: false,
    chartArea: {
			backgroundColor: 'rgba(255, 255, 255, 1)'
				},
    animation: {
        onComplete: done
    }
}

// Roept de methode done niet aan omdat het dashboard het niet nodig heeft. Options is ook om grafiekeigenschappen aan te passen voor het dashboard.
var dashboardOptions = {
    bezierCurve : false,
    width:100,
    height:200,
    maintainAspectRatio: false
}

// dataSet en options worden in de willMount geselecteerd afhankelijk van de props.
let dataSet;
let options;

export class BarChart extends React.Component {

    // Voor het renderen word de dataset geselecteerd. Dataset is de parameter in de url.
    componentWillMount() {
        // apiOptions genereert een afbeelding, dashboardOptions doet dat niet.
        switch(this.props.goal) {
            case "dashboard":
                options = dashboardOptions;
                break;
            case "api":
                options = apiOptions;
                break;
            default:
                options = dashboardOptions;
        }
        // 'id' geeft de dataset aan. indien niet aangegeven of foutief word het d1.
        switch(this.props.id) {
            case "d1":
                dataSet = d1;
                break;
            case "d2":
                dataSet = ["TestSet"];
                break;
            default:
                dataSet = d1;
            }
        };

    render() {
        return(
            <Bar ref={ref => this.chart = ref} data={dataSet} options={options} />
        );
    }
};



