import React from 'react';
import { Graph } from '../components/Graph.js';
import { Doughnuts } from '../components/Doughnuts.js';
import { BarChart } from '../components/BarChart.js';
import { Radars } from '../components/Radars.js';
import { Scatter } from '../components/Scatter.js';
import { PolarPie } from '../components/PolarPie.js';
import { Chart } from 'react-chartjs-2';

// plugin zorgt ervoor dat de achtergrondkleur van de grafiek gewijzigd kan worden, normaliter is dit niet mogelijk.
// geef in de grafiek component 'apiOptions' aan welke kleur het moet worden, let op het is RGBA
Chart.pluginService.register({
    beforeDraw: function (chart, easing) {
        if (chart.config.options.chartArea && chart.config.options.chartArea.backgroundColor) {
            var helpers = Chart.helpers;
            var ctx = chart.chart.ctx;
            var canvas = chart.canvas;
            var chartArea = chart.chartArea;
            const canvas = chart.canvas
            ctx.save();
            ctx.fillStyle = chart.config.options.chartArea.backgroundColor;
            ctx.fillRect(0,0,canvas.width, canvas.height);
            ctx.restore();
        }
    }
});

const goal = "api";
// pakt de props uit de URL en returned de juiste grafiek met de juiste dataset.
function GraphType(props) {
    switch(props.type) {
        case "graph":
            return <Graph goal={goal} id={props.id} />;
            break;
        case "barchart":
            return <BarChart goal={goal} id={props.id} />;
            break;
        case "doughnut":
            return <Doughnuts goal={goal} id={props.id} />;
            break;
        case "radar":
            return <Radars goal={goal} id={props.id} />;
            break;
        case "scatter":
            return <Scatter goal={goal} id={props.id} />;
            break;
        case "polarpie":
            return <PolarPie goal={goal} id={props.id} />;
            break;
        default:
            return <Graph goal={goal} id={props.id} />;
    }
}

class ApiGraph extends React.Component {
    render() {

            return (
                <div>
                    <img id="graph" />
                    <div style={{visibility: 'hidden'}}>
                        <GraphType type={this.props.match.params.type} id={this.props.match.params.dataSet} />
                    </div>
                </div>
            );
    };
}
export default ApiGraph;



